export default async function getWeatherData(city) {
  const url = new URL(
    `https://api.openweathermap.org/data/2.5/weather?q=${city}&units=metric&appid=${process.env.REACT_APP_OPEN_WEATHER_API_KEY}`
  );

  return fetch(url)
    .then((res) => {
      return res.json();
    })
    .then((json) => {
      // console.clear();
      return json;
    });
}

export async function getCityFromCoord(lat, lon) {
  const url = new URL(
    `https://api.openweathermap.org/geo/1.0/reverse?lat=${lat}&lon=${lon}&appid=${process.env.REACT_APP_OPEN_WEATHER_API_KEY}`
  );

  return fetch(url)
    .then((res) => res.json())
    .then((json) => {
      // console.clear();
      return json;
    });
}
