import React from "react";

export default function List(props) {
  return (
    <li className="list-group-item note-list">
      <h4 className="card-title">{props.note.title}</h4>
      <p className="card-text">{props.note.content}</p>
      <p className="note-date">
        {props.note.date.dd}/{props.note.date.mm}/{props.note.date.yyyy}
      </p>
      <button className="btn btn-danger" onClick={props.deleteNote}>
        DELETE
      </button>
    </li>
  );
}
